.slider-container {
    width: 380px;
    height:210px;
    margin: 0 auto;
    border: 2px solid grey;
    margin-top: 60px;
    overflow: hidden;
}

.slider {
    display: flex;
}

.slider div {
    width: 380px;
    height: 210px;
    flex-shrink: 0;
    flex-basis: 100%;
}

i {
    position: absolute;
    border-radius: 100%;
    color: grey;
    font-size: 30px;
}

#nextBtn {
    top: 45%;
    right: 5px;
}

#prevBtn {
    top: 45%;
    left: 5px;
}

.wrapper {
    width: 660px;
    position: relative;
    margin: 0 auto;
}

.pic1 {
    background-image: url("/pic1.jpg");
    background-size: 100%;
}

.pic2 {
    background-image: url("/pic2 .jpg");
    background-size: 100%;
}

.pic3 {
    background-image: url("/pic3.jpg");
    background-size: 100%;
}

.pic4 {
    background-image: url("/pic4.jpg");
    background-size: 100%;
}

.pic5 {
    background-image: url("/pic5.jpg");
    background-size: 100%;
}

.pic6 {
    background-image: url("/pic6.jpg");
    background-size: 100%;
}

.pic7 {
    background-image: url("/pic7.jpg");
    background-size: 100%;
}

.slider{
    transform:translateX(380px );
}

@media only screen and (max-width:686px) {
    .wrapper {
        width: 100%;
    }
    .slider-container {
        width: 380px;
        height: 210px;
    }
}

@media only screen and (min-width:686px){
    #nextBtn{
        right: 50px;
    }
    #prevBtn{
        left: 50px;
    }
}
